import Observer from "./Observer";
import Lottie from "react-lottie";
import animationData from '../lotties/backend'

const DevelopTwo = ({ dimension }) => {

const leftText = true;
const text = "Furthermore, I can do backend based on nodejs, and express server. Moreover, saving the data to database."


const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
  };

    return(
        <Observer leftText={leftText} text={text}>
            <div className="lottie">
            <Lottie 
	         options={defaultOptions}
             height={dimension.height}
            width={dimension.wide}
            />
            </div>
        </Observer>
    )
}
export default DevelopTwo;