import Observer from "./Observer";
import Lottie from "react-lottie";
import animationData from '../lotties/student'
import { useDimension } from "../store/Store";

const About = () => {

const leftText = false;
const text = "I’m a software development and entreprenaurship student and an optimistic programmer. Currently, pursing my studies at EUAS, Tallinn."

const dimension = useDimension(state => state.data)
console.log(dimension)

const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
  };

    return(
        <Observer leftText={leftText} text={text}>
            <div className="lottie">
            <Lottie 
	         options={defaultOptions}
             height={dimension.height}
             width={dimension.wide}
            />
            </div>
        </Observer>
    )
}
export default About;