import { Container, Fade, Grid } from "@material-ui/core";
import { useInView } from "react-intersection-observer";
import { BsDownload } from 'react-icons/bs';
import { IconContext } from "react-icons";
import fb from '../assets/fb.svg';
import git from '../assets/github.svg'
import linked from '../assets/linkedIn.svg'

const Footer = () => {

    const { ref, inView } = useInView({
        triggerOnce: false,
        threshold: 0.9
      });

    return(
     <section  className="foot" ref={ref}>
        <Fade in={inView}>
            <Container className="section">
                <Grid container 
                className="section"
                justifyContent='flex-start'
                alignItems="flex-start"
                >
                    <Grid 
                    container item 
                    className="ext"
                    lg={12}
                    >
                       <p id='start-text'> To know more about me, feel free to contact me.</p>
                    </Grid>

                    <Grid
                    container item 
                    className="ext"
                    lg={12}
                    > 
                         <Grid item lg={3} md={3} sm={1}>
                        </Grid>
                        <Grid item lg={2} md={2} sm={3} xs={12}>
                            <ImageTag src={fb} text='facebook'
                            link='https://fb.com/Shubhdeeps/'
                            />
                        </Grid>
                        <Grid item lg={2} md={2} sm={3} xs={12}>
                            <ImageTag src={git} text='github' 
                            link='https://github.com/Shubhdeeps/'
                            />
                        </Grid>
                        <Grid item lg={2} md={2} sm={3} xs={12}>
                            <ImageTag src={linked} text='linkedIn' 
                            link='https://www.linkedin.com/in/shubhdeep-singh'
                            />
                        </Grid>
                        <Grid item lg={3} md={3} sm={1}>
                        </Grid>
                    </Grid>
                    <Grid 
                    container item
                    className="ext"
                    justifyContent="flex-end"
                    alignItems="center"
                    lg={12}
                    > <p id='end-text'> Or download my resume    
                    </p>
                    <IconContext.Provider value={{ size: '2em' }}>
                     <BsDownload />
                    </IconContext.Provider>
                    </Grid>
                </Grid>
                </Container>   
        </Fade>
     </section>
    )
}

const ImageTag = ({ src, text, link}) => {
    const handleClick = () => {
        window.open(link)
    }
    return(
        <div className="image" onClick={() => handleClick()}>
            <img alt='social' src={src} width={120}/>
            <p>{text}</p>
        </div>
    )
}


export default Footer;