import { Container, ImageList, ImageListItem, ImageListItemBar, makeStyles } from "@material-ui/core";
import astro from '../assets/astro.png'
import enum1 from '../assets/enum1.png'
import enum2 from '../assets/enum2.png'
import enumApi from '../assets/enumApi.png'
import weather from '../assets/weather.png'


const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
    },
    imageList: {
      flexWrap: 'nowrap',
      // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
      transform: 'translateZ(0)',
    },
    title: {
      color: theme.palette.primary.light,
    },
    titleBar: {
      background:
        'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },
  }));


const feedData = [
    {
        img: astro,
        title: 'Astrology',
        url: 'https://shubhdeeps.github.io/Astrology/'
    },
    {
        img: enum1,
        title: 'E-numbers - I',
        url: 'https://shubhdeeps.github.io/e-numbers/'
    },
    {
        img: enum2,
        title: 'E-numbers - II',
        url: 'https://shubhdeeps.github.io/enum/'
    },
    {
        img: weather,
        title: 'Weather app',
        url: 'https://shubhdeeps.github.io/weather/'
    },
    {
        img: enumApi,
        title: 'API (express server)',
        url: 'https://shubhdeeps.herokuapp.com/api/e-numbers'
    }
]
const Feed = () => {
    const classes = useStyles();

    const handleClick = (link) => {
        window.open(link)
    }
    return(
        <Container className="feed">
            <p>
            Below are few of my own developed web application. These are based on reactjs.
            </p>
            <ImageList className={classes.imageList} cols={2.5}>
                {feedData.map((items) => (
                    <ImageListItem 
                    onClick={() => handleClick(items.url)}
                    key={items.img} >
                         <img src={items.img} alt='project' />
                         <ImageListItemBar
                          title={items.title}
                          classes={{
                              root: classes.tittleBar,
                              tittle: classes.title,
                          }}
                          />
                    </ImageListItem>
                ))}
            </ImageList>
        </Container>
    )
}


export default Feed;