import { Grid, Grow, Slide } from '@material-ui/core';


const Layout = ({ inView, leftText, children, text, title }) => {
    return(
        <Grid
          container
          className='grid-container'
          justifyContent='center'
          alignItems='center'
        > 
            <Grid container item
            lg={2}
            xs={12}
            >

            </Grid>
            <Grid container item
            justifyContent='center'
            lg={4}
            md={4}
            sm={6}
            xs={12}
            >
               {
                 leftText ? <Sliding inView={inView} dir='right' text={text} title={title} /> :
                 <Fading inView={inView} children={children} />
               }
            </Grid>
            <Grid container item
            justifyContent='center'
            lg={4}
            md={4}
            sm={6}
            xs={12}
            >
              {
                leftText ? <Fading inView={inView} children={children} /> :
                <Sliding inView={inView} dir='up' text={text} title={title} />
              }
            </Grid>
            <Grid container item
            lg={2}
            xs={12}
            >

            </Grid> 
        </Grid> 
    )
}


const Fading = ({ inView, children }) => {
  return(
    <Grow in={inView}> 
     {children}
   </Grow>
  )
}
const Sliding = ({ inView, dir, text, title }) => {
  const style = {
    textRight: {
      color: '#0B1D22',
      fontSize: '22px',
      textAlign: 'right',
      padding: '50px'
    },
    textLeft: {
      color: '#0B1D22',
      fontSize: '22px',
      textAlign: 'left',
      padding: '50px'
    }
  }
  return(
    <Slide 
    in={inView}
    direction={dir}
    >
          <p className={title ? 'title' : ""} style={dir === 'right' ? style.textLeft : style.textRight} >
            {text}
         </p>
    </Slide>
  )
}



export default Layout;