import { Box, makeStyles } from '@material-ui/core';
import React from 'react';
import About from './component/About';
import Develop from './component/Develop';
import DevelopTwo from './component/Developtwo';
import Feed from './component/Feed';
import Footer from './component/Footer';
import Home from './component/Home';
import Java from './component/Java';
import Skills from './component/Skills';
import { useDimension } from './store/Store';
import './stylesheet/style.css'

const App = () => {

   const setDimensions = useDimension(state => state.setData)

   if(window.matchMedia('(min-width: 1300px)').matches){
    setDimensions({
      wide: 600,
      height: 600
    })
   }
   else if(window.matchMedia('(min-width: 1000px)').matches){
    setDimensions({
      wide: 400,
      height: 600
    })
  }
  else if(window.matchMedia('(min-width: 700px)').matches){
    setDimensions({
      wide: 400,
      height: 400
    })
  }
  else if(window.matchMedia('(min-width: 200px)').matches){
    setDimensions({
      wide: 250,
      height: 250
    })
  }

  const dimension = useDimension(state => state.data)

  const classes = useStyles();
  return (
    <div className='App'>
    <Box className={classes.pageOne} >
      <Home dimension={dimension} />
    </ Box >
    <Box className={classes.pageTwo} >
      <About dimension={dimension} />
    </ Box >
    <Box className={classes.pageThree} >
      <Skills dimension={dimension} />
    </ Box >
    <Box className={classes.pageFour} >
      <Develop dimension={dimension} />
    </ Box >
    <Box className={classes.pageFive} >
      <DevelopTwo dimension={dimension} />
    </ Box >
    <Box className={classes.pageTwo} >
      <Java dimension={dimension} />
    </ Box >
    <Box className={classes.pageFour} >
      <Feed />
    </ Box >
    <Box className={classes.pageOne}>
      <Footer />
    </ Box >
    </div>
  );
}


const useStyles = makeStyles({
  pageOne: {
      background: 'linear-gradient(116.26deg, #343434 0.56%, #000000 100.51%)',
      width: '100%',
      height: '100vh',
      scrollSnapAlign: 'start'
  },
  pageTwo: {
    background: 'linear-gradient(116.26deg, #DFDDC7 0.80%, #F5E3D6 100.51%)',
    width: '100%',
    height: '100vh',
    scrollSnapAlign: 'start'
},
pageThree: {
  background: 'linear-gradient(116.26deg, #FFEAEA 0.80%, #E1D5BD 100.51%)',
  width: '100%',
  height: '100vh',
  scrollSnapAlign: 'start'
},
pageFour: {
  background: 'linear-gradient(116.26deg, #E4FEFF 0.80%, #E6FFE2 100.51%)',
  width: '100%',
  height: '100vh',
  scrollSnapAlign: 'start'
},
pageFive: {
  background: 'linear-gradient(116.26deg, #E6FFE2 0.80%, #A8EEC0 100.51%)',
  width: '100%',
  height: '100vh',
  scrollSnapAlign: 'start'
},
});



export default App;
