import Observer from "./Observer";
import person from '../assets/person.png'

const Home = ({ wide, height }) => {
const text = "Hello, I'm Shubhdeep Singh"


    return(
        <Observer leftText={false} text={text} title={true}>
            <div className="lottie">
            <img alt='person' src={person} id='person'  />
            
            </div>
        </Observer>
    )
}
export default Home;