import Observer from "./Observer";
import Lottie from "react-lottie";
import animationData from '../lotties/skills'

const Skills = ({ wide, height }) => {
const leftText = true;
const text = "My skills includes Javascript, Reactjs, Java, Css, HTML, nodejs, Figma, Adobe XD, Object oriented programming, functional programming and much more... "

const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
  };

    return(
        <Observer leftText={leftText} text={text}>
            <div className="lottie">
            <Lottie 
	         options={defaultOptions}
             height={height}
            width={wide}
            />
            </div>
        </Observer>
    )
}
export default Skills;