import Observer from "./Observer";
import Lottie from "react-lottie";
import animationData from '../lotties/develop'

const Develop = ({ wide, height }) => {

const leftText = false;
const text = "I can develop frontend web applications based on html, css, javascript, react js and next js. Along with state management with redux/zustand and user interface/experience using material ui and framer motion, and much more... "


const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
  };

    return(
        <Observer leftText={leftText} text={text}>
            <div className="lottie">
            <Lottie 
	         options={defaultOptions}
             height={height}
            width={wide}
            />
            </div>
        </Observer>
    )
}
export default Develop;