
import { useInView } from 'react-intersection-observer';
import Layout from './Layout';




const Observer = ( { children, leftText, text, title }) => {

    const { ref, inView } = useInView({
        triggerOnce: false,
        threshold: 0.8
      });

    return(
        <section className='section' ref={ref}>
               <Layout inView={inView} leftText={leftText} children={children}  text={text} title={title} />
        </section>
    )
}

export default Observer;